
import HeaderCom from '@/components/common/HeaderCom'
import ScrollList from '@/components/common/ScrollList'
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import Vue from 'vue'
export default {
name: 'ticketList',
components: {
XButton,
HeaderCom,
ScrollList,
Icon,
CheckIcon
},
data () {
return {
title: '',
inlineDescList: [],
inlineDescListValue: [],
ticketListArr:[],
appId: '79340',
sign: '9e151ce583aa4403bd3d3df0d894374e',
cityName: '',
param: {

}
}
},
computed: {
},
methods: {
...mapMutations("CONTAINER", ["setOptions"]),
commitTicket () {
var ticketArr = []
var seatTypeArr = []
for (let i = 0; i < this.ticketListArr.length; i++) {
if(this.ticketListArr[i].status){
ticketArr.push(this.ticketListArr[i].num)
for (let j = 0; j < this.ticketListArr[i].tickets.length; j++) {
seatTypeArr.push( this.ticketListArr[i].tickets[j].ticketName)
}
}
}
console.log(seatTypeArr)
this.setOptions({
trainNumberList: ticketArr,
trainNumberStr: ticketArr.join(""),
currentSeatTypeArr : seatTypeArr
});
this.$router.push({name: 'index'})
},
chooseTicket(item,idx) {
item.status = !item.status;
Vue.set(this.ticketListArr,idx,item)
console.log(this.ticketListArr)
},
change (val, label) {
console.log('change', val, label)
},
getDataList (val) {
var that = this
this.$store.dispatch('TICKET/getTicketList',val).then(res => {
if(res.data.showapi_res_code == -1) {
console.log('')
return
}
var Data = res.data.showapi_res_body.trains
console.log(Data)
for (let i = 0; i < Data.length; i++) {
Data[i].status = false
var hour = parseInt(Data[i].usedTime / 60);
var minute = Data[i].usedTime % 60;
Data[i].usedTimeFormat = hour + '' + minute + ''

let tickets = [];
let ticket = Data[i].ticketInfo;
if (typeof (ticket.softsleeperdown) != "undefined") {  //
tickets.push({ 'ticketName': ticket.softsleeperdown.ticketName, isTicket: ticket.softsleeperdown.ticketNum == '0' ? '' : '' });
}
if (typeof (ticket.hardsleepermid) != "undefined") { //
tickets.push({ 'ticketName': ticket.hardsleepermid.ticketName, isTicket: ticket.hardsleepermid.ticketNum == '0' ? '' : '' });
}
if (typeof (ticket.hardseat) != "undefined") { //
tickets.push({ 'ticketName': ticket.hardseat.ticketName, isTicket: ticket.hardseat.ticketNum == '0' ? '' : '' });
}
if (typeof (ticket.dsleeperdown) != "undefined") { //
tickets.push({ 'ticketName': ticket.dsleeperdown.ticketName, isTicket: ticket.dsleeperdown.ticketNum == '0' ? '' : '' });
}
if (typeof (ticket.businessseat) != "undefined") { //
tickets.push({ 'ticketName': ticket.businessseat.ticketName, isTicket: ticket.businessseat.ticketNum == '0' ? '' : '' });
}
if (typeof (ticket.firstseat) != "undefined") { //
tickets.push({ 'ticketName': ticket.firstseat.ticketName, isTicket: ticket.firstseat.ticketNum == '0' ? '' : '' });
}
if (typeof (ticket.secondseat) != "undefined") { //
tickets.push({ 'ticketName': ticket.secondseat.ticketName, isTicket: ticket.secondseat.ticketNum == '0' ? '' : '' });
}
if (typeof (ticket.noseat) != "undefined") { //
tickets.push({ 'ticketName': ticket.noseat.ticketName, isTicket: ticket.noseat.ticketNum == '0' ? '' : '' });
}
Data[i].tickets = tickets
}
this.ticketListArr = Data
console.log(this.ticketListArr )
})
}
},
computed: {
...mapGetters("CONTAINER", [
"origin",
"destination",
"specifiedDate",
"passenger",
"seatType",
"selectDayList",
"todayTime"
])
},
mounted() {
this.param = {}
this.param.showapi_appid = this.appId;
this.param.showapi_sign = this.sign;
this.param.from = this.origin;
this.param.to = this.destination;
this.param.trainDate = this.selectDayList[0];
this.getDataList(this.param);
},
}



